<template>
  <div v-show="id">
    <h3>电话医生设置</h3>
      <p>服务区域：{{ptPhoneDoctor.cityCodes}}</p>
      <p>服务次数：{{ptPhoneDoctor.serverTimes}}</p>
      <p>单次时长：{{ptPhoneDoctor.singleDuration}}</p>
      <p>自费购买：{{ptPhoneDoctor.selfPay}}</p>
      <p>等待期：{{ptPhoneDoctor.waitMonths}}</p>
  </div>
</template>

<script>
export default {
  name: 'youDoctor',
  props: {
    id: {
      type: Number,
      default: 0
    }
  },
  data() {
    return {
      ptPhoneDoctor: {}
    }
  },
  methods: {
    getData () {
      this.axios.get(`/goods/phoneDoctor/${this.id}`).then((res) => {
        const code = Number(res.data.code)
        if (code === 200) {
          this.ptPhoneDoctor = res.data.data
        } else {
          this.$message(this.mes.busy)
        }
      }).catch((err) => {
        console.log(err)
        this.$message(this.mes.busy)
      })
    }
  },
  watch: {
    id() {
      this.getData()
    }
  }
}
</script>

