<template>
  <div v-show="id">
    <h3>押金垫付设置</h3>
    <p>服务区域：{{ptCashPrePay.cityCodes}}</p>
    <p>服务次数：{{ptCashPrePay.serverTimes}}</p>
    <p>等待期：{{ptCashPrePay.waitMonths}}</p>
    <p>沟通确认提示文案: {{ptCashPrePay.remark}}</p>
  </div>
</template>

<script>
export default {
  name: 'youPash',
  props: {
    id: {
      type: Number,
      default: 0
    }
  },
  data() {
    return {
      ptCashPrePay: {}
    }
  },
  methods: {
    getData () {
      this.axios.get(`/goods/cashPrePay/${this.id}`).then((res) => {
        const code = Number(res.data.code)
        if (code === 200) {
          this.ptCashPrePay = res.data.data
        } else {
          this.$message(this.mes.busy)
        }
      }).catch((err) => {
        console.log(err)
        this.$message(this.mes.busy)
      })
    }
  },
  watch: {
    id() {
      this.getData()
    }
  }
}
</script>

